// {"title":"AngleSharp v0.9.6 released","date":"2016-05-06 13:27:00","categories":"news release","author":"Florian Rappl","userId":"FlorianRappl","section":"Posts","link":"/blog/2016-05-06-anglesharp-v96-released","source":"/home/runner/work/anglesharp.github.io/anglesharp.github.io/docs/blog/2016-05-06-anglesharp-v96-released.md"}
import * as React from 'react';
import { ContentPage } from 'piral-docs-tools/components';

      import { PageContent, Markdown, getPageLayout } from 'piral-docs-tools/components';

      const PageLayout = getPageLayout("blog");
      const link = "https://github.com/AngleSharp/anglesharp.github.io/tree/main/docs/blog/2016-05-06-anglesharp-v96-released.md";
      const html = `
<p>More than two months after the release of AngleSharp v0.9.5 the new version v0.9.6 has been published. Besides some bug fixes the version focused on starting some changes regarding the final API touches and modifications.</p>
<p>The version may contain breaking changes to anyone using AngleSharp. Two of things that will potentially break builds are concerned with API renaming (e.g., <code>Submit</code> becomes <code>SubmitAsync</code>) and the removal of the <code>IEventAggregator</code>. While the former can be solved easily, the second one may actually require a little bit more time. Dropping the <code>IEventAggregator</code> was necessary to provide a uniform API that can also be exposed in JavaScript.</p>
<p>Additionally, the usage of <code>new Configuration()</code> is highly <em>discouraged</em>. If you create plain new configurations <strong>no factories</strong> will be available to parser, requesters, ... - a lot of components. This will result in ill-behavior. I advise everyone to use <code>Configuration.Default</code> as the basis for any configuration adjustments.</p>
<p>Finally, the scripting library (<code>AngleSharp.Scripting.JavaScript</code>) has been updated - supporting the latest version of the core AngleSharp library and bringing some fixes and improvements. Together with some changes in the core library the experience should be much more complete right now, but there is still some way to go.</p>
<p>The samples, demo projects, and the <code>AngleSharp.Io</code> extension library have been updated accordingly. They all work seamlessly together and will be unified with the release of v1.0.0, planned later this year.</p>
<p>Thanks to everyone for their contribution!</p>
`;
      const meta = {"title":"AngleSharp v0.9.6 released","date":"2016-05-06 13:27:00","categories":"news release","author":"Florian Rappl","userId":"FlorianRappl","section":"Posts"};
    

export default () => (
  <ContentPage>
    
      <PageLayout meta={meta}>
        <PageContent meta={meta}>
          <Markdown content={html} link={link} editLabel={"Edit on GitHub"} />
        </PageContent>
      </PageLayout>
    
  </ContentPage>
);